











































import {Vue, Component} from 'vue-property-decorator';
import {Form as ElForm, Input} from 'element-ui';
import {UserModule} from '@/store/modules/user';
import GVerify from 'type-gverify';

@Component
export default class Login extends Vue {
  private checked = false;
  private loginForm = {
    username: '',
    password: '',
    code: '',
  };
  private verifyCode: any;

  private validateUsername = (rule: any, value: string, callback: any) => {
    if (!value) {
      callback(new Error(`请输入正确的用户名`));
    } else {
      callback();
    }
  }

  private validatePassword = (rule: any, value: string, callback: any) => {
    if (value.length < 4) {
      callback(new Error(`密码不能少于4位`));
    } else {
      callback();
    }
  }

  private loginRules = {
    username: [{validator: this.validateUsername, trigger: 'blur'}],
    password: [{validator: this.validatePassword, trigger: 'blur'}],
  };

  private handleLogin() {
    (this.$refs.loginForm as ElForm).validate(async (valid: boolean) => {
      if (valid) {
        const {username, password, code} = this.loginForm;
        if (!(this.verifyCode).validate(code)) {
          this.$message.error('验证码错误');
          return;
        }
        const data = await UserModule.Login({username, password});
        if (data) {
          if (this.checked) {
            UserModule.rememberUserHanded({username, password});
          } else {
            UserModule.removeRememberUserHanded();
          }
          this.$message({
            message: '登录成功',
            type: 'success',
            duration: 1000,
            onClose: () => {
              this.$router.push({
                path: '/',
              });
            },
          });
        }
      } else {
        return false;
      }
    });
  }

  private mounted() {
    if (UserModule.rememberUser) {
      const {username, password} = JSON.parse(UserModule.rememberUser);
      this.loginForm.username = username;
      this.loginForm.password = password;
      this.checked = true;
    } else {
      if (this.loginForm.username === '') {
        (this.$refs.username as Input).focus();
      } else if (this.loginForm.password === '') {
        (this.$refs.password as Input).focus();
      }
    }

    this.verifyCode = new GVerify({
      id: 'v_container',
    });
  }

}
